<template>
  <div class="logout" >
   <h1 class="title">注销账号: </h1>
         <hr>
         <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>提示:注销账号后,您将不能继续使用此系统</span>
          </div>
          <div class="text">
          <el-form label-position="center" label-width="120px" class="form" :model="logoutData"  :disabled='true'>
                <el-form-item label="所注销账号:">
                     <el-input v-model="logoutData.userid"></el-input>
                </el-form-item>
           <el-form-item label="账号所属人员:">
                 <el-input v-model="logoutData.name"></el-input>
            </el-form-item>
          </el-form>
             <el-button type="text" @click="open">注销账号</el-button>
          </div>
        </el-card>
        

  </div>
</template>

<script>
export default {
   data() {
      return {
      //  logoutData:this.$store.getters.get_Data,
      logoutData:JSON.parse(sessionStorage.getItem('userinfo')),
      };
    },
    methods: {
       open() {
        this.$confirm('此操作将永久注册此账号, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.del();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      out(){
        this.$axios.post('/API/outinfo',{
         }).then(res => { 
           if(res.data.code=='1'){
            this.$router.replace({path:'/login'});
           }
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
         })
      },
      del(){
      let param = new URLSearchParams()
      param.append('username',this.logoutData.userid)
      this.$axios.post("/API/delete",param).then(res => {
          if(res.data.code=='1'){
              sessionStorage.removeItem("userinfo");
              this.$message.success(res.data.message);
              this.out();
           }else{
            this.$message.error(res.data.message);
          }
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
        }) 
      }
 }
}
</script>


<style scoped>

 .title{
   width: 100%;
   text-align: left;
   height: 60px;
   line-height: 60px;
   padding-left: 20px;
 }
  .text {
    font-size: 14px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    width: 480px;
    margin:  20px  130px;
  }
</style>
